
.footer{position:fixed;bottom:0;left:0;z-index:100;width:100%;height:2.66667rem;background-color:#f4f4f4
}
.footer div{position:relative;border-top:1px solid #ddd
}
.footer a{position:relative;height:2.66667rem;display:block;float:left;width:50%;text-align:center;box-sizing:border-box
}
.footer a span{display:block;margin:.26667rem auto 0;width:1.06667rem;height:1.06667rem;font-size:1.06667rem;color:#999
}
.footer a p{color:#999;font-size:.58667rem
}
.footer a.active span,.footer a.active p{color:#f9696c
}



