
.com-article{position:relative;background-color:#f4f4f4;margin-bottom:3.2rem
}
.com-article .box{position:relative
}
.com-article .imgbox img{width:100%;height:10.66667rem
}
.com-article .art-detail{position:relative;margin-top:.53333rem;background-color:#fff;padding:.4rem;box-sizing:border-box
}
.com-article .user{position:relative;height:3.2rem;background-color:#fff
}
.com-article .user time{position:absolute;right:.4rem;bottom:.4rem
}
.com-article .user img{display:block;width:2.66667rem;height:2.66667rem;margin:.26667rem 0 0 .26667rem;border-radius:100%;float:left
}
.com-article .user .name{width:10.66667rem;float:left;margin-left:.53333rem
}
.com-article .user .name p{padding-top:.4rem;line-height:2.3rem;color:#333;font-size:.8rem
}
.com-article .user .tag{position:relative;top:0;width:100%
}
.com-article .user .tag span{display:inline-block;width:1.6rem;height:.8rem;line-height:.8rem;font-size:.37333rem;text-align:center;border:1px solid #f9696c;background-color:#fff;border-radius:.13333rem;color:#f9696c;margin-right:.26667rem
}
.com-article .user .btn-d{position:absolute;right:.4rem;top:.66667rem
}
.operate{font-size:.8rem;padding:5px 10px;background-color:white;margin-top:.2rem;color:gray
}
.zan{padding-right:1.4rem
}
.zan span{padding:0 .1rem
}
.hasZan span:first-child{color:#f9696c
}
.kanguo{padding-right:25px
}
.kanguo span{padding:0 .1rem
}
.starList{background-color:white;margin-top:1px;padding:5px 10px;font-size:0.7rem;color:#f9696c
}
.imgbox{overflow:hidden
}
.imgbox ul{width:100%
}
.imgbox ul li{width:33%;float:left
}
.imgbox ul li img{height:7.66667rem !important
}
.r3{width:100% !important
}
.Userstar{position:absolute;right:10px;top:1px
}



