
.header{position:relative;top:0;width:20rem;height:2.66667rem;text-align:center;box-sizing:border-box;line-height:2.66667rem;background-color:#f9696c
}
.header .add-msg{position:absolute;right:.8rem;top:0.001rem;font-size:1.4rem;color:white
}
.header .title p{color:#fff;text-align:center;font-size:.85333rem
}
.header .t-icon{width:1.33333rem;height:1.33333rem;display:block;top:.66667rem
}
.header .t-icon span{width:100%;height:100%;display:block;line-height:1.33333rem;color:#fff
}
.header .back{position:absolute;left:.53333rem
}
.header .share{position:absolute;right:.53333rem
}
.header .place{position:relative;left:0;float:left;width:4rem;line-height:2.66667rem;text-align:center;color:#fff;font-size:.8rem
}



