
.index{position:relative
}
.index .center{display:-ms-flexbox;display:flex;margin-top:.53333rem;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;background-color:#fff
}
.index .center .actived p{color:#f9696c !important
}
.index .center .list{-ms-flex-positive:1;flex-grow:1;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;padding:.53333rem 0
}
.index .center .list p{text-align:center;color:#666;font-size:.8rem
}
.index .center .list span{display:block;margin:0 auto;font-size:1.6rem;line-height:1.6rem
}
.index .btn{width:2.66667rem;height:1.06667rem;display:block;margin:0 auto
}
select{position:relative;height:30px;line-height:30px;margin-top:2px;padding:0 10px;width:100%;background-color:white;border:1px solid #f1f1f1;border-radius:0;font-size:16px
}
.arrow{position:absolute;top:214px;right:20px;content:'';display:block;width:8px;height:8px;border-top:1px solid #666;border-left:1px solid #666;transform-origin:50%;transform:rotate(-135deg)
}



