
.weui_mask_transparent{position:fixed;top:0;left:0;z-index:98;width:100%;height:100%;background:transparent
}
.weui_toast{position:fixed;top:180px;left:50%;z-index:99;margin-left:-3.8em;width:7.6em;min-height:7.6em;border-radius:5px;background:rgba(40,40,40,0.75);color:#fff;text-align:center
}
.weui_icon_toast{display:block;margin:22px 0 0
}
.weui_icon_toast:before{color:#fff;content:'\EA08';font-size:55px
}
.weui_toast_content{margin:0 0 15px
}
.weui_loading_toast{position:absolute;z-index:105
}
.weui_loading_toast .weui_toast_content{margin-top:64%;font-size:14px
}
.weui_loading{position:absolute;top:38%;left:50%;z-index:2000000000;width:0
}
.weui_loading_leaf{position:absolute;top:-1px;opacity:.25
}
.weui_loading_leaf:before{position:absolute;width:8.14px;height:3.08px;border-radius:1px;background:#d1d1d5;box-shadow:0 0 1px rgba(0,0,0,0.0980392);content:" ";transform-origin:left 50% 0
}
.weui_loading_leaf_0{animation:a 1.25s linear infinite
}
.weui_loading_leaf_0:before{transform:rotate(0) translate(7.92px)
}
.weui_loading_leaf_1{animation:b 1.25s linear infinite
}
.weui_loading_leaf_1:before{transform:rotate(30deg) translate(7.92px)
}
