}
.weui_loading_leaf_5{animation:f 1.25s linear infinite
}
.weui_loading_leaf_5:before{transform:rotate(150deg) translate(7.92px)
}
.weui_loading_leaf_6{animation:g 1.25s linear infinite
}
.weui_loading_leaf_6:before{transform:rotate(180deg) translate(7.92px)
